import Swal from "sweetalert2";

export const sendWithToken = ( data, endpoint ) => {
    
    Swal.fire({
        title: 'Enviando',
        text: 'Por favor espere...',
        allowOutsideClick: false,
        didOpen: () => {
            Swal.showLoading();
        }
    });

    const requestOptions = {
        method: 'POST',
        headers: {
            'Content-type':'application/json',
            'authorization':'c17fb5a54d11675ebd57d8f99bc060d7c79b'
        },
        body: JSON.stringify(
            data
        )
    };

    fetch(`https://backend-curriculum-ronco.herokuapp.com/${endpoint}`, requestOptions)
        .then( response => 
            Swal.fire(
                'Enviado!',
                '',
                'success'
            )
        )
        .catch( error =>{ 
            console.log(error);
            Swal.fire(
                'Ocurrio un problema',
                'Por favor intente mas tarde',
                'error'
            )
        });
}

